.collectioncard {
  position: relative;
  margin: 20px;
  min-width: 200px;
  min-height: 225px;
  display: inline-flex;

  &:hover {
    .pops {
      transform: scale(1.05);
    }

    .popshadow {
      transform: perspective(1.8rem) rotateX(30deg) !important;
      opacity: 0.5 !important;
    }
  }

  .pops {
    display: flex;
    position: absolute;
    margin-top: 104px;
    z-index: 2;
    pointer-events: none;
    transition: transform 300ms cubic-bezier(0.03, 0.98, 0.52, 0.99);
    margin-right: 10px;
    justify-content: flex-end;
    width: calc(100% - 10px);

    img {
      max-width: 95px;
    }

    div {
      z-index: -1;
      filter: blur(2px);
      height: 100%;
      left: 0;
      position: absolute;
      top: 43px;
      width: 100%;
      margin-right: 10px;
      display: flex;
      justify-content: flex-end;

      .popshadow {
        filter: brightness(0) drop-shadow(0 -20px 5px rgba(0, 0, 0, 0.25))
          blur(0) drop-shadow(0 0 0 black);
        height: 50px;
        mask-image: linear-gradient(135deg, transparent, rgba(0, 0, 0, 0.75));
        opacity: 0.75;
        transform: perspective(2rem) rotateX(45deg);
        transform-origin: bottom left;
        width: 100%;
        transition: all 300ms cubic-bezier(0.03, 0.98, 0.52, 0.99);
      }
    }
  }

  .collectioninfo {
    padding: 20px;

    p {
      font-weight: normal;
      opacity: 0.5;
      filter: drop-shadow(0px 0px 5px rgba(0, 0, 0, 0.5));
      margin-bottom: 10px;

      &:first-child {
        font-weight: 600;
        opacity: 0.85;
        font-size: 25px;
      }
    }
  }

  .tiltcard {
    position: relative;
    width: 600px;
    background-color: rgba(255, 255, 255, 0.05);
    border-radius: 10px;
    box-shadow: 2px 2px 25px -5px rgba(0, 0, 0, 0.4);
    border: 1px solid transparent;

    .js-tilt-glare {
      border-radius: 10px;
      // background: radial-gradient(ellipse at top left, #FFFFFF3D 0%, #FFF0 100%);
      background: radial-gradient(
        ellipse at top right,
        #ffffff3d 0%,
        #fff0 100%
      );
    }
  }
}
