#statistics {
  flex-direction: column;

  > div {
    display: flex;
    margin: 20px 0;

    &.firstgraphs {
      > div:first-child {
        width: 66%;
      }
      > div:last-child {
        width: 33%;
      }
    }

    &.secondgraphs {
      > div:first-child {
        width: 33%;
        margin-right: 20px;
      }
      > div:last-child {
        width: 66%;
      }
    }

    canvas {
      padding: 20px;
    }

    .card {
      width: calc(95%);
    }
  }
}
