.card {
  position: relative;

  .notilt {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    top: 0;
    left: 0;
    z-index: 2;
    pointer-events: none;
  }

  .tilt {
    background: radial-gradient(ellipse at top right, #ffffff3d 0%, #fff0 100%);
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.05);
    z-index: 2;
    box-shadow: 2px 2px 25px -5px rgba(0, 0, 0, 0.4);

    .js-tilt-glare {
      border-radius: 10px;
    }
  }
}
