* {
  margin: 0;
  padding: 0;
  color: white;
  font-family: "Norms", "Segoe UI";
  font-size: 16px;
  text-decoration: none;
}

#root {
  min-height: 101vh;
}

ul {
  list-style: none;
}

h1 {
  font-size: 24px;
}

h2 {
  font-size: 22px;
}

h3 {
  font-size: 20px;
}

p,
a {
  font-size: 18px;
}

main {
  /*width: calc(100% - 100px);
  height: calc(100% - 100px);
  max-width: 1169px;
  padding: 50px;*/
  margin: 0 auto;
  max-width: 1200px;

  > div {
    display: flex;
    flex-wrap: wrap;
  }
}

@media screen and (max-width: 900px) {
  #header nav {
    margin-right: 0 !important;
  }
  main {
    width: 85%;

  #statistics > div {
    display: block;
    > div {
      width: 100% !important;
      margin-bottom: 50px;
    }
  }

  #categories {
    .collectioncard {
      margin: 20px 0;
    }
  }

  #statistics > div.secondgraphs > div:first-child {
  margin-right: 20px;
  width: 75%;
  }

  #pop {
    display: block;
    height: auto !important;

    > div {
      margin-bottom: 75px;
      width: 100% !important;
    }
  }
}
}
