.searchcard {
  position: relative;
  margin: 20px;
  min-width: 200px;
  min-height: 225px;
  display: inline-flex;

  &:hover {
    .searchimage img {
    }
  }

  .searchimage {
    overflow: hidden;
    // margin: 20px;
    background-color: white;
    height: 100%;
    width: 225px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    border-radius: 10px 0 0 10px;

    img {
      max-width: 205px;
      max-height: 205px;
      margin: 10px;
    }
  }

  .searchinfo {
    margin-left: 225px;
    height: calc(100% - 40px);
    padding: 20px;

    p {
      font-weight: normal;
      opacity: 0.5;
      filter: drop-shadow(0px 0px 5px rgba(0, 0, 0, 0.5));
      margin-bottom: 10px;

      &:first-child {
        font-weight: 600;
        opacity: 0.85;
      }
    }
  }

  .tiltcard {
    width: 600px;
    background-color: rgba(255, 255, 255, 0.05);
    border-radius: 10px;
    box-shadow: 2px 2px 25px -5px rgba(0, 0, 0, 0.4);
    border: 1px solid transparent;

    .js-tilt-glare {
      border-radius: 10px;
      // background: radial-gradient(ellipse at top left, #FFFFFF3D 0%, #FFF0 100%);
      background: radial-gradient(
        ellipse at top right,
        #ffffff3d 0%,
        #fff0 100%
      );
    }
  }
}
