#search {
  flex-direction: column;
  display: block;

  input {
    display: block;
    border: 0;
    padding: 10px;
    font-size: 25px;
    width: calc(600px - 20px);
    margin: 0 20px 20px;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 10px;
    box-shadow: 2px 2px 25px -5px rgba(0, 0, 0, 0.4);
    margin: 40px 20px;
  }

  > p {
    margin: 40px 25px;
  }

  button {
    display: block;
    margin: 20px;
    margin-bottom: 40px;
    padding: 5px 20px;
    border: none;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 5px;

    &:hover {
      background-color: rgba(255, 255, 255, 0.3);
    }
  }
}
