#pop {
  flex-wrap: nowrap;
  justify-content: space-between;
  height: calc(100vh - 69px);

  a {
    color: #fff;
    cursor: pointer;
    font-size: 25px;
    font-weight: 600;
    text-decoration: none;
    transition: all 0.25s;
    display: block;
  }

  > div:first-child {
    width: 400px;

    > * {
      margin-top: 30px;
    }

    .popview {
      &:hover {
        .notilt {
          transform: scale(1.05);
        }
      }
      .notilt {
        overflow: visible;
        height: 400px;
        width: 400px;
        margin-top: 30px;
        transition: transform 300ms cubic-bezier(0.03, 0.98, 0.52, 0.99);

        div {
          z-index: -1;
          filter: blur(2px);
          height: 100%;
          width: 100%;
          left: 0;
          position: absolute;
          top: 0;

          img {
            transform-origin: bottom left;
            transform: perspective(16rem) rotateX(69deg);
            position: absolute;
            opacity: 0.75;
            mask-image: linear-gradient(
              135deg,
              transparent,
              rgba(0, 0, 0, 0.75)
            );
            filter: brightness(0) drop-shadow(0 -20px 5px rgba(0, 0, 0, 0.25))
              blur(0) drop-shadow(0 0 0 black);
            z-index: -1;
          }
        }
        img {
          height: 100%;
          width: 100%;
          position: absolute;
          filter: drop-shadow(-2px 5px 20px rgba(0, 0, 0, 0.25)) contrast(115%);
        }
      }

      .tilt {
        padding-top: calc(400px + 40px);
        padding-bottom: 15px;
      }
      h1 {
        margin: 15px 30px 0;
        font-size: 25px;
        text-transform: uppercase;
      }

      p {
        margin: 10px 30px;
        font-size: 20px;
        opacity: 0.5;
      }
    }

    .popstickers {
      --width: 100px;
      height: 162px;
      // height: 100px;
      section {
        height: 100%;
      }
      .tilt {
        height: 100%;
        padding: 20px;
        display: flex;
        // align-items: center;

        > div {
          text-align: center;
          width: var(--width);
          display: flex;
          flex-direction: column;
          justify-content: end;
          align-items: center;
          margin-right: 10px;

          img {
            max-width: 100px;
            max-height: 80px;
          }

          h1 {
            margin-top: 10px;
            font-size: 17px;
            text-transform: capitalize;
          }

          p {
            text-transform: uppercase;
            font-size: 14px;
            opacity: 0.75;
          }
        }
      }
    }
  }

  > div:last-child {
    width: calc(1200px - 400px - 30px);

    > * {
      margin-top: 30px;
    }
    .tilt {
      width: 100%;
    }

    .popinfo {
      height: calc(100% - 400px - 30px - 80px);

      section,
      .tilt {
        height: 100%;
      }

      .tilt {
        display: flex;
      }

      ul {
        padding-top: 20px;
        li {
          font-size: 20px;
          margin: 15px 47px;
          display: flex;
          align-items: center;

          .galery {
            align-items: center;
            background: #fff;
            display: flex;
            height: 150px;
            justify-content: center;
            min-height: 0;
            padding: 5px;
            position: relative;
            width: 150px;
            border-radius: 10px;

            img {
              max-height: 100%;
              max-width: 100%;
              height: auto;
              width: auto;
            }

            span {
              background-color: #000;
              border-radius: 10px;
              color: #fff;
              margin: 5px;
              opacity: 0.75;
              padding: 5px 10px;
              position: absolute;
              right: 0;
              top: 0;
            }
          }

          a {
            font-size: 20px;
            color: lightblue;

            &:hover {
              text-decoration: underline;
            }
          }

          img {
            height: 30px;
            width: 30px;
            margin-left: 10px;
          }

          /*svg:first-child {
          height: 23px;
          width: 30px;
          margin-right: 10px;
          margin-left: 0;
          fill: rgba(255, 255, 255, 0.25);
        }*/

          svg {
            height: 16px;
            width: 16px;
            margin-left: 5px;
            fill: white;
          }

          span {
            margin-left: 10px;
            font-size: 20px;
            font-weight: 700;
          }
        }
      }
    }

    .popgraph {
      height: 400px;

      section,
      .tilt {
        height: 100%;
      }
    }
  }
}
