.popcard {
  position: relative;
  margin: 20px;
  height: min-content;

  &.popok .tiltcard {
    // background-color: rgba(255, 255, 255, 0.1);

    &.love {
      // background-color: rgba(255, 0, 0, 0.1);
      box-shadow: 0 0 180px -15px rgba(255, 0, 0, 0.15) inset;
    }
  }

  &.popnotok .tiltcard {
    background-color: rgba(255, 255, 255, 0.1);

    &.love {
      box-shadow: 0 0 180px -15px rgba(255, 0, 0, 0.1) inset;
    }

    .js-tilt-glare {
      background: none;
    }
  }

  &:hover {
    .popimage {
      transform: scale(1.1);
    }

    .popshadow {
      transform: perspective(1.2rem) rotateX(20deg) !important;
      opacity: 0.5 !important;
    }
  }

  .blurcard {
    width: 100%;
    height: 100%;
    position: absolute;
    filter: blur(5px);
  }

  .popimage {
    width: 200px;
    height: 200px;
    position: absolute;
    top: 0;
    left: 0;
    margin: 20px auto;
    transition: transform 300ms cubic-bezier(0.03, 0.98, 0.52, 0.99);
    z-index: 2;
    pointer-events: none;

    svg {
      height: 30px;
      width: 30px;
      top: 175px;
      right: 15px;
      position: absolute;
      fill: #f1686a;
      filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.5));
      animation: heart 0.85s infinite alternate;
      opacity: 0.5;
    }

    img {
      width: 100%;
      height: 100%;
    }

    > img {
      position: absolute;
      // filter: drop-shadow(-2px 5px 50px rgba(255, 255, 255, 0.5)) contrast(115%);
      filter: drop-shadow(-2px 5px 20px rgba(0, 0, 0, 0.25)) contrast(115%);
    }

    div {
      z-index: -1;
      filter: blur(2px);
      height: 100%;
      left: 0;
      position: absolute;
      top: 0;
      width: 100%;

      .popshadow {
        filter: brightness(0) drop-shadow(0 -20px 5px rgba(0, 0, 0, 0.25))
          blur(0) drop-shadow(0 0 0 black);
        height: 50px;
        mask-image: linear-gradient(135deg, transparent, rgba(0, 0, 0, 0.75));
        opacity: 1;
        position: absolute;
        top: 145px;
        transform: perspective(2rem) rotateX(45deg);
        transform-origin: bottom left;
        width: 100%;
        transition: all 300ms cubic-bezier(0.03, 0.98, 0.52, 0.99);
      }
    }
  }

  .tiltcard {
    // height: 100%;
    padding-bottom: 15px;
    width: 200px;
    // min-height: 300px;
    background-color: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border: 1px solid transparent;
    border-radius: 10px;
    z-index: 2;
    // filter: drop-shadow(2px 2px 5px rgba(0, 0, 0, 10.25));
    box-shadow: 2px 2px 25px -5px rgba(0, 0, 0, 0.4);
    border: 1px solid transparent;
    // background: radial-gradient(ellipse at top left, #FFFFFF3D 0%, #FFF0 100%);
    > p:first-child {
    }

    h1,
    p {
      text-decoration: none;
      margin: 0 18px;
      font-weight: normal;
      opacity: 0.5;
      filter: drop-shadow(0px 0px 5px rgba(0, 0, 0, 0.5));
      font-size: 16px;
      transform: translateZ(0);
    }

    h1 {
      text-transform: uppercase;
      margin-left: 20px;
      font-weight: 600;
      opacity: 0.85;
      margin-bottom: 5px;
      margin-top: calc(200px + 40px);
      font-size: 17px;

      span {
        font-size: 13px;
      }
    }

    p {
      display: flex;
      align-items: baseline;
      opacity: 0.75;
      white-space: nowrap;

      .estimated {
        font-size: 15px;
        opacity: 0.75;

        svg {
          fill: white;
        }
      }

      .price {
        color: gold;
        font-weight: 500;
        margin-right: 4px;

        svg {
          fill: gold;
          margin-right: 3px;
        }
      }

      svg {
        height: 16px;
        width: 16px;
        transform: translateY(2px);

        &:last-child {
          height: 12px;
          width: 12px;
          transform: translateY(0px);
          margin-left: 2px;
        }
      }
    }

    .crossed {
      // text-decoration: line-through;
    }

    .stickers {
      display: flex;
      height: 40px;
      margin: 10px 18px 0;

      img {
        filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.25));
        height: 100%;
        margin-right: 10px;
        max-width: 65px;
      }

      &.minimal {
        img {
          max-width: 40px;
          width: 100%;
          height: fit-content;
        }
      }
    }

    .js-tilt-glare {
      border-radius: 10px;
      // background: radial-gradient(ellipse at top left, #FFFFFF3D 0%, #FFF0 100%);
      background: radial-gradient(
        ellipse at top right,
        #ffffff3d 0%,
        #fff0 100%
      );
    }
  }
  @keyframes heart {
    0% {
      transform: scale(1.05);
    }
    25% {
      transform: scale(0.9);
    }
    100% {
      transform: scale(1);
    }
  }
}
