#header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  height: 69px;
  background-color: rgba(255, 255, 255, 0.05);
  box-shadow: 2px 2px 25px -5px rgba(0, 0, 0, 0.4);
  overflow: hidden;
  background: radial-gradient(ellipse at top right, #ffffff26 0%, #fff0 100%);
  margin-bottom: 20px;

  nav {
    margin-right: calc(25% - 69px);
    height: 100%;

    ul {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;

      li {
        // margin-left: 25px;
        height: 100%;

        a {
          padding: 0 25px;
          text-decoration: none;
          opacity: 0.5;
          height: 100%;
          display: flex;
          align-items: center;

          &:hover {
            opacity: 0.6;
          }

          &.active {
            opacity: 1;
          }
        }
      }
    }
  }
}
